@media screen and (min-width: 1350px) {
    .main {
        width: 1325px;
        margin: auto;
    }
}
body {
    margin: 0px;
    padding: 0;
}
.header {
    width: 100%;
    height: 60px;
    display: flex;
}
.logo_img {
    height: 55px;
    margin: 0px;
    margin-left: 10%;
    margin-top: 2px;
}
.nav {
    margin-top: 0px;
    margin-left: 20%;
    height: 60px;
    list-style-type: none;
    font-size: 0.8em;
    line-height: 60px;
}
.nav a {
    text-decoration: none;
    color: black;
 }
.nav li {
    display: inline;
    margin-right: 20px;
    cursor: pointer;
    padding: 6px;
}
.nav li:hover {
    color: white;
    background-color: #ffbb42;
    border-radius: 5px;
}
.nav:last-child {
    margin-right: 0px;
}
.nav:first-child {
    margin-left: 30%;
}
.hide_nav {
    width: 40px;
    height: 40px;
    display: none;
    position: absolute;
    top: 18px;
    right: 10px;
    border: none;
    background: none;
    background-image: url(menu.png);
    background-size: 35px;
    background-repeat: no-repeat;
}
.hide_nav:hover {
    background-image: url(menu-hover.png);
    background-size: 35px;
    background-repeat: no-repeat;
    cursor: pointer;
}
.hide_nav_pan {
    display: none;
    list-style-type: none;
    position: absolute;
    top: 40px;
    right: 40px;
}

.hide_nav_pan a {
    text-decoration: none;
    color: black;
    margin-top: 20px;
}
.hide_nav_pan a:hover {
    color: gray;
}


@media (max-width: 1100px) {
    .nav {
        font-size: 0.6em;
        margin-left: 10%;
    }
}
@media (max-width: 905px) {
    .nav {
        margin-left: 5%;
    }
    .logo_img {
        margin-left: 2%;
    }
}
@media (max-width: 790px) {
    .nav {
        display: none;
    }
    .hide_nav {
        display: block;
    }
}


.banner {
    width: 100%;
    height: 740px;
    background: linear-gradient(#709dca 90%, white 10%);
    text-align: center;
    color: white;
}
.banner_mainText {
    font-weight: bolder;
    line-height: 25px;
    margin: 0px;
    padding: 0px;
    margin-bottom: 5px;
    padding-top: 150px;
}
.banner_text {
    margin: 0px;
    padding: 0px;
    font-weight: lighter;
}
.callbackForm {
    width: 60%;
    height: 50px;
    margin: auto;
    margin-top: 20px;
    display: flex;
}
.callbackForm input {
    display: flexbox;
    width: 20%;
    height: 35px;
    margin: 0px;
    padding: 0px;
    margin-right: 2%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.callbackForm_icons {
    display: flexbox;
    background-color: white;
    background-image: url(email.png);
    background-repeat: no-repeat;
    background-position: center;
    width: 40px;
    height: 35px;
    margin: 0;
}
.callbackForm_icons:first-child {
    background-image: url(name.png);
}
.phone_icon {
    background-image: url(tel.png);
}
.callbackForm input:last-child {
    margin-right: 0px;
    height: 34px;
    background-color: #ffbb42;
    border: none;
    color: white;
    font-size: 0.6em;
}
.callbackForm input:last-child:hover {
    background-color: #ffdb9c;
}
.callbackForm_text {
    border: none;
}
.callbackForm_text::placeholder {
    text-align: left;
}
.callbackForm_text:focus {
    outline: none;
}
.callbackForm_text:hover {
    outline: 2px solid #4e6c8a;
}




.banner_slider {
    width: 650px;
    height: 340px;
    margin: auto;
    margin-top: 100px;
    background-image: url(laptop.png);
    background-size: 100%;
    background-repeat: no-repeat;
}
.banner_slider_pic {
    width: 70%;
    margin: auto;
    margin-top: 18px;
}
.banner_sliderControl {
    width: 100px;
    height: 50px;
    margin: auto;
    margin-top: 10px;
}

@media screen and (max-width: 700px) {
    .callbackForm {
        display: block;
        height: 250px;
    }
    .callbackForm input {
        width: 150px;
        margin: 0px;
        margin-bottom: 15px;
        vertical-align: top;
    }
    .callbackForm_icons {
        width: 30px;
        display: inline-block;
        margin: 0px;
    }
    .banner_slider {
        width: 350px;
        height: 200px;
        margin-top: 10px;
    }
    .banner_slider_pic {
        margin-top: 10px;
    }
}
@media screen and (max-width: 420px) {
    .banner_slider {
        width: 90%;
        height: 190px;
    }
}
@media screen and (max-width: 370px) {
    .banner_slider {
        height: 170px;
    }
    .banner_slider_pic {
        margin-top: 6px;
    }
}
@media screen and (max-width: 345px) {
    .banner_slider {
        height: 155px;
    }
    .callbackForm {
        height: 220px;
    }
    .callbackForm input {
        width: 100px;
    }
}



