    vertical-align: top;
    margin-top: 20px;
    margin-left: 15px;
    width: 80%;
}
.service_grid_part_text_main {
    font-size: 0.9em;
    margin: 0px;
    margin-bottom: 20px;
    padding: 0px;
    text-align: left; 
}
.service_grid_part_text_low {
    font-size: 0.8em;
    margin: 0px;
    padding: 0px;  
    text-align: left;  
}



@media screen and (max-width: 1030px) {
    .service_grid {
        width: 95%;
    }
}
@media screen and (max-width: 875px) {
    .service_grid {
        display: block;
    }
    .service_grid_part {
        height: 90px;
        padding: 5px;
    }
    .service_grid_part_text_main {
        margin-bottom: 10px;
    }
}
@media screen and (max-width: 500px) {
    .service_grid_part_icon {
        height: 150px;
    }
    .service_grid_part_text {
        margin-top: 10px;
    }
    .service_grid_part_text_main {
        font-size: 0.8em;
        margin-bottom: 5px;
    }
    .service_grid_part_text_low {
        font-size: 0.7em;
    }
    .service_mainText {
        padding-top: 25px;
    }
    .service_grid_part {
        height: 110px;
        padding: 0px;
    }
    .service_grid {
        height: 680px;
    }
}
@media screen and (max-width: 350px) {
    .service_grid_part_icon {
        height: 110px;
    }
}


