.pricing {
    width: 100%;
    height: 700px;
    color: #394b50;
    text-align: center;
    background-color: #e7edf3;
}
.pricing_mainText {
    margin: 0;
    padding: 0;
    padding-top: 50px;
}
.pricing_text {
    margin: 0;
    padding: 0;
}
.pricing_table {
    display: flex;
    width: 70%;
    height: 600px;
    margin: auto;
    overflow: hidden;
}
.pricing_table_part:hover {
    height: 500px;
    margin-top: 50px;
    color: white;
    background-color: #709dca;
}
.pricing_table_part:hover .pricing_table_part_text {
    border-color: white;
}
.pricing_table_part:hover .pricing_table_part_main {
    padding-top: 50px;
}
.pricing_table_part {
    width: 33%;
    background-color: white;
    text-align: center;
    height: 400px;
    color: black;
    margin-top: 100px;
    overflow: hidden;
}
.pricing_table_part:first-child {
    margin-right: 0.5%;
}
.pricing_table_part:last-child {
    margin-left: 0.5%;
}
.pricing_table_part_text {
    width: 80%;
    font-size: 0.8em;
    margin: auto;
    margin-bottom: 15px;
    padding-top: 15px;
    border-top: 0.1px solid black;
}
.pricing_table_part_main {
    font-size: 1.6em;
}
.pricing_table_part_price {
    line-height: 20px;
    font-size: 2.3em;
    font-weight: 900;
    color: #ffbb42;
    margin: 0;
    padding: 0;
    margin-bottom: 40px;
}
.pricing_table_part_price span {
    font-size: 0.4em;
    font-weight: 100;
    color: black;
    margin: 0;
    padding: 0;
}
.pricing_table_part:hover .pricing_table_part_price {
    color: white;
}
.pricing_table_part:hover span {
    color: white;
}
.pricing_table_part_button {
    width: 150px;
    height: 40px;
    margin: auto;
    border: none;
    color: white;
    background-color: #ffbb42;
    margin-top: 20px;
}
.pricing_table_part_button:hover {
    cursor: pointer;
    background-color: #ffc155;
}
.pricing_table_sliderButton {
    display: none;
}


@media screen and (max-width: 920px) {
    .pricing_table_part_main {
        font-size: 1.3em;
    }
    .pricing_table_part_text {
        font-size: 0.6em;
    }
}
@media screen and (max-width: 800px) {
    .pricing_table {
        width: 90%;
    }
}
@media screen and (max-width: 600px) {
    .pricing_table {
        width: 100%;
    }
    .pricing_table_part_button {
        width: 90%;
    }
}
@media screen and (max-width: 535px) {
    .pricing_table {
        height: 500px;
    }
    .pricing_table_part:hover {
        height: 400px;
    }
    .pricing_table_part:hover .pricing_table_part_main {
        padding-top: 0px;
    }
    .pricing_table_part {
        width: 70%;
        margin: auto;
        animation-duration: 0.7s;
    }
    .pricing_table_part:first-child {
        display: none;
        margin: auto;
        animation-duration: 0.7s;
    }
    .pricing_table_part:last-child {
        display: none;
        margin: auto;
        animation-duration: 0.7s;
    }
    .pricing_table_sliderButton {
        display: block;
        width: 40px;
        height: 40px;
        float: left;
        position: relative;
        top: 35%;
        background-color: none;
        border: none;
        border-radius: 30px;
        background-image: url('previos.png');
    }
    .a {
        float: right;
        background-image: url('next.png');
    }
    .pricing_table_sliderButton:hover {
        background-color: #fbfcff;
    }
    @keyframes slidein {
        from {
            margin-right: -100%;
        }
        to {
            margin-right: 15%;
        }
    }
    @keyframes slideinSpec {
        from {
            margin-right: -200%;
        }
        to {
            margin-right: -100%;
        }
    }
    @keyframes slideout {
        from {
            margin-left: -100%;
        }
        to {
            margin-left: 15%;
        }
    }
    @keyframes slideoutSpec {
        from {
            margin-left: -200%;
        }
        to {
            margin-left: -100%;
        }
    }
    @keyframes outleft {
        from {
            margin-left: 15%;
        }
        to {
            margin-left: -100%;
        }
    }
    @keyframes outleftSpec {
        from {
            margin-left: 15%;
            margin-right: 15%;
        }
        to {
            margin-left: -100%;
            margin-right: 145%;
        }
    }
    @keyframes outright {
        from {
            margin-right: 15%;
        }
        to {
            margin-right: -100%;
        }
    }
    @keyframes outrightSpec {
        from {
            margin-right: 15%;
            margin-left: 15%;
        }
        to {
            margin-right: -100%;
            margin-left: 145%;
        }
    }
}





.howItWork {
    width: 100%;
    height: 700px;
    text-align: center;
}
.howItWork_mainText {
    color: #394b50;
    margin: 0;
    padding: 70px 0 0 0;
}
.howItWork_text {
    color: #394b50;
    margin: 0 0 50px 0;
    padding: 0;
}
.howItWork_button {
    width: 150px;
    height: 50px;
    margin: auto;
    color: white;
    border: none;
    margin: 40px 0 0 0;
    background-color: #ffbb42;
}
.howItWork_button:hover {
    color: #ffc155;
    background-color: white;
    outline: 2px solid #ffc155;
}
.howItWork_main {
    display: flex;
    width: 80%;
    height: 300px;
    margin: auto;
    padding-top: 40px;
}
.howItWork_main_step {
    width: 23%;
    margin: auto;
    margin-top: 0;
    margin-bottom: 0;
}
.howItWork_main_step_mainText {
    font-weight: 100;
    font-size: 1.4em;
    margin: 90px 0 10px 0;
    padding: 0;
    color: #394b50;
}
.howItWork_main_step_text {
    width: 90%;
    font-size: 0.7em;
    margin: 0;
    margin: auto;
    padding: 0;
    color: #394b50;
}
.arrow {
    position: absolute;
    top: 2980px;
    left: 25%;
}
.down {
    top: 3050px;
    left: 45%;
}
.secondUp {
    left: 65%;
}

@media screen and (max-width: 1150px) {
    .down {
        left: 44%;
    }
}
@media screen and (max-width: 1100px) {
    .arrow {
        width: 118px;
    }
}
@media screen and (max-width: 1050px) {
    .arrow {
        width: 108px;
        height: 30px;
    }
}
@media screen and (max-width: 950px) {
    .arrow {
        display: none;
    }
}
@media screen and (max-width: 550px) {
    .howItWork_main {
        width: 95%;
        text-align: center;
        display: grid;
        padding: 0;
        height: 400px;
        grid-template-columns: 100%;
        grid-template-rows: 25% 25% 25% 25%;
    }
    .howItWork_main_step {
        display: inline;
        width: 100%;
    }
    .howItWork_main_step_mainText {
        margin: 0;
        padding: 0;
    }
    .howItWork_main_step_text {
        width: 90%;
        margin: 0;
        padding: 0;
    }
    .howItWork_main_step_img {
